.input_wrapper{position:relative}
.plastic_select, input[type=url], input[type=text], input[type=tel], input[type=number], input[type=email], input[type=password], select, textarea {
    font-size: 1.25rem;
    line-height: normal;
    padding: .75rem;
    border: 1px solid #C5C5C5;
    border-radius: .25rem;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    outline: 0;
    color: #555459;
    width: 100%;
    max-width: 100%;
    font-family: Slack-Lato,appleLogo,sans-serif;
    margin: 0 0 .5rem;
    -webkit-transition: box-shadow 70ms ease-out,border-color 70ms ease-out;
    -moz-transition: box-shadow 70ms ease-out,border-color 70ms ease-out;
    transition: box-shadow 70ms ease-out,border-color 70ms ease-out;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    box-shadow: none;
    height: auto;
}
.pincode-input-container
{
  .pincode-input-text
  {
    padding:0 !important;
    margin:0 2px;
    text-align:center;
    border: 1px solid;
    background: transparent;
    width: 50px;
    height: 50px;
  }
  .pincode-input-text:focus
  {
    outline:none;
    box-shadow:none;
  }
}

















.card-signin {  border: 0;  border-radius: 1rem;  box-shadow: 0 0.5rem 1rem 0 rgba(0, 0, 0, 0.1);}
.card-signin .card-title { margin-bottom: 2rem; font-weight: 300; font-size: 1.5rem;}

.card-signin .card-body {  padding: 2rem;}

.form-signin {  width: 100%;}

.form-signin .btn {  font-size: 80%;  border-radius: 5rem;  letter-spacing: .1rem;  font-weight: bold;  padding: 1rem;  transition: all 0.2s;
}

.form-label-group {  position: relative;  margin-bottom: 1rem;}

.form-label-group input {  height: auto;  border-radius: 2rem;}

.form-label-group>input,
.form-label-group>label {  padding: var(--input-padding-y) var(--input-padding-x);}

.form-label-group>label{position: absolute;top: 0;left: 0;display: block;width: 100%;margin-bottom: 0;line-height: 1.5;color: #495057;border: 1px solid transparent;border-radius: .25rem;transition: all .1s ease-in-out}

.form-label-group input::-webkit-input-placeholder {  color: transparent;}

.form-label-group input:-ms-input-placeholder { color: transparent;}

.form-label-group input::-ms-input-placeholder {  color: transparent;}

.form-label-group input::-moz-placeholder {  color: transparent;}

.form-label-group input::placeholder {  color: transparent;}

.form-label-group input:not(:placeholder-shown) {
  padding-top: calc(var(--input-padding-y) + var(--input-padding-y) * (2 / 3));  padding-bottom: calc(var(--input-padding-y) / 3);}

.form-label-group input:not(:placeholder-shown)~label {
  padding-top: calc(var(--input-padding-y) / 3);  padding-bottom: calc(var(--input-padding-y) / 3);  font-size: 12px;  color: #777;}

