.footer_section h2{ color:#fff;font-family: 'Poppins', sans-serif; font-size:23px; position:relative;    margin-bottom: 30px;}
.footer_section h2:after {
    position: absolute;
    border-bottom: 2px solid #fff;
    content: "";
    width: 51px;
    left: 0;
    bottom: -6px;
}
.footer_section ul li h6:hover {color:#000;}
ul.foot_menu {padding-left: 20px;}
.footer_section .foot_social li{float:left;list-style:none;padding: 0 2px;}
.footer_section .foot_social li .fa{ border-radius:50%; background:#fff; height:30px; width:30px; line-height:32px; text-align:center;}
.footer_section .foot_social li .fa.fa-facebook:hover { background:#3b5998 !important ; color:#fff}
.footer_section .foot_social li .fa.fa-youtube:hover { background:#c4302b  ; color:#fff}
.footer_section .foot_social li .fa.fa-twitter:hover { background:#00acee  ; color:#fff}
.footer_section .foot_social li .fa.fa-pinterest:hover { background:#c8232c  ; color:#fff}
.footer_section .foot_social li .fa.fa-instagram:hover { background:#f13cda ; color:#fff}

.footer_section .foot_social{ padding-left:0;}
.footer_section .foot_menu li{ font-size:16px;color:#fff;}
.footer_section .foot_menu li a{ font-size:14px; color:#fff;}

.copyright{ background:#0461a3;  }
.copyright p{ color:#fff; margin-bottom:0; font-size:13px;    margin-bottom: 0px !important;    padding-left: 20px;
    padding-top: 20px; text-transform:uppercase;}

@media(max-width:500px)
{
.footer_section img{ max-width:130px!important}
.footer_section .foot_social {
float: right;
padding-top: 13px;
padding-right: 0px;
}
}

/*--------------foooter dashboard-----------------*/
.footer_dash { width: 100%; overflow: hidden; margin-top: 40px; box-shadow: 0px 0px 13px -7px #000; -moz-box-shadow: 0px 0px 13px -7px #000; -webkit-box-shadow: 0px 0px 13px -7px #000; -ms-box-shadow: 0px 0px 13px -7px #000; }
.footer_dash .copyright { background:#fff; padding: 20px 10px; }
.footer_dash .copyright p { padding: 0px; margin-bottom: 0px; color:#333; text-align: center;  }


/*add css by rohit kumar*/
.footer_section { padding: 10px 0px 10px; border-bottom: 1px solid #ddd; background:-webkit-linear-gradient( 0deg, rgb(151,38,143) 0%, rgb(237,20,90) 100%); }
.footer_logo { height: auto; }
.footer_section .foot_social { margin-bottom: 0px; padding: 5px 0px; }
.footer_section img { max-width: 145px; }
footer .copyright { padding: 10px 0px; background:-webkit-linear-gradient( 0deg, rgb(151,38,143) 0%, rgb(237,20,90) 100%); }
footer .copyright p { padding: 0px !important; line-height: 20px; text-align: center;}
footer .footer_section .foot_social li a { color:#000; }
footer .footer_section .foot_social li .fa.fa-linkedin:hover { color:#fff; background:#1a7de4; }
footer .footer_section .foot_social li .fa.fa-youtube-play:hover { color:#fff; background:#9a2929; }
footer ul.foot_menu.term_page { padding: 5px 0px; float:none; width: 100%; overflow:hidden; text-align: center; }
footer ul.foot_menu.term_page li { display: inline-block; float:none; }
footer ul.foot_menu.term_page li a { color:#fff; line-height: 20px; }
